<template>
	<view class="icon" :class="['icon-'+type]" :style="{color:color,'font-size':fontSize}" @click="onClick()"></view>
</template>

<script>
	export default {
		name: 'uni-icon',
		props: {
			/**
			 * 图标类型
			 */
			type: String,
			/**
			 * 图标颜色
			 */
			color: String,
			/**
			 * 图标大小
			 */
			size: [Number, String]
		},
		computed: {
			fontSize() {
				return `${this.size}px`
			}
		},
		methods: {
			onClick() {
				this.$emit('click')
			}
		}
	}
</script>

<style>
	@font-face {
		font-family: uniicons;
		font-weight: normal;
		font-style: normal;
		src: url(data:font/truetype;charset=utf-8;base64,) format('truetype');
	}

	.icon {
		font-family: uniicons;
		font-size: 24px;
		font-weight: normal;
		font-style: normal;
		line-height: 1;
		display: inline-block;
		text-decoration: none;
		-webkit-font-smoothing: antialiased;
	}

	.icon.active {
		color: #007aff;
	}

	.icon-huishou:before {
	  content: "\e633";
	}
	
	.icon-home-smile-2-line:before {
	  content: "\e613";
	}
	
	.icon-home-smile-2-fill:before {
	  content: "\e614";
	}
	
	.icon-bubble-chart-fill:before {
	  content: "\e622";
	}
	
	.icon-bubble-chart-line:before {
	  content: "\e623";
	}
	
	.icon-shopping-bag-fill:before {
	  content: "\e8d3";
	}
	
	.icon-shopping-bag-line:before {
	  content: "\e8d4";
	}
	
	.icon-user-smile-fill:before {
	  content: "\ece9";
	}
	
	.icon-user-smile-line:before {
	  content: "\ecea";
	}
</style>
